<template>
    <div id="detailContrainer" class="slide-enter-active">
        <nHeader title="影片详情">
            <i class="iconfont icon-right" @touchstart="handleToBack"></i>
        </nHeader>
        <Loading v-if="isLoading" />
		<div v-else id="content" class="contentDetail">
			<div class="detail_list">
				<div class="detail_list_bg" :style="{ 'background-image' : 'url('+ detailList.img.replace(/w\.h/,'148.208') +')' }"></div>
				<!-- style 内不支持过滤器的写法 -->
				<div class="detail_list_filter"></div>
				<div class="detail_list_content">
					<div class="detail_list_img">
						<img :src="detailList.img | setWH('148.208')">
					</div>
					<div class="detail_list_info">
						<h2>{{detailList.nm}}</h2>
						<p>{{detailList.enm}}</p>
						<p>{{detailList.sc}}</p>
						<p>{{detailList.cat}}</p>
						<p>{{detailList.fra}} / {{detailList.dur}}分钟</p>
						<p>{{detailList.pubDesc}}</p>
					</div>
				</div>
			</div>
			<div class="detail_intro">
				<p>{{detailList.dra}}</p>
			</div>
			<div class="detail_player swiper-container" ref="detail_player">
				<ul class="swiper-wrapper">
					<li class="swiper-slide" v-for="(item, index) in detailList.photos" :key="index">
						<div>
							<img :src="item | setWH('140.127')">
						</div>
					</li>
				</ul>
			</div>
		</div>
	</div>
</template>
<script>
import nHeader from "@/components/Header/nheader"
import Loading from "@/components/Loading/loading"
export default {
	name: "detail",
	data: function(){
		return{
			isLoading: true,
			detailList: [],
		}
	},
	components:{
		nHeader,
		Loading,
	},
	methods: {
		handleToBack: function(){
			// 设置路由返回上一级
			this.$router.go(-1);
			// 或者 this.$router.back(-1);  括号内的值是代表返回的步数
		}
	},
	props: ["movieID"],
	created:function(){
		// console.log(this.movieID);
		var _this = this;
		this.$http.get("/api/detailmovie?movieId="+this.movieID).then(function(res){
			// console.log(res.data);
			if(res.data.status === 0){
				_this.detailList = res.data.data.detailMovie;
				// console.log(_this.detailList.img);
				_this.isLoading = false;
				// 注意这里是swiper的动画，必须在页面DOM加载完毕之后在进行swiper的动画
				_this.$nextTick(function(){
					new Swiper(_this.$refs.detail_player, {
						slidesPerView: "auto",
						freeMode: true,
						freeModeSticky: true,
					})
				});
			}
		})
		
	}
}
</script>
<style lang="scss" scoped>
#detailContrainer{ position: absolute; left:0; top:0; z-index: 100; width:100%; min-height:100%; background:#fff;}
#detailContrainer.slide-enter-active{ animation:.3s slideMove;}
@keyframes slideMove{
    0%{ transform : translateX(100%); }
    100%{ transform : translateX(0); }
}
#content.contentDetail{ display: block; margin-bottom:0;}
#content .detail_list{ height:200px; width:100%; position: relative; overflow: hidden;}
.detail_list .detail_list_bg{ width:100%; height:100%; background: 0 40%; filter: blur(20px); background-size:cover; position: absolute; left: 0; top: 0;}
.detail_list .detail_list_filter{ width:100%; height:100%; position: absolute;background-color: #40454d;opacity: .55; position: absolute; left: 0; top: 0; z-index: 1;}
.detail_list .detail_list_content{ display: flex; width:100%; height:100%; position: absolute; left: 0; top: 0; z-index: 2;}
.detail_list .detail_list_img{ width:108px; height: 150px; border: solid 1px #f0f2f3; margin:20px;}
.detail_list .detail_list_img img{ width:100%; height: 100%;}
.detail_list .detail_list_info{ margin-top: 20px;}
.detail_list .detail_list_info h2{ font-size: 20px; color:white; font-weight: normal; line-height: 40px;}
.detail_list .detail_list_info p{ color:white; line-height: 20px; font-size: 14px; color:#ccc;}
#content .detail_intro{ padding: 10px;}
#content .detail_player{ margin:20px;}
.detail_player .swiper-slide{ width:70px; margin-right: 20px; text-align: center; font-size: 14px;}
.detail_player .swiper-slide img{ width:100%; margin-bottom: 5px;}
.detail_player .swiper-slide p:nth-of-type(2){ color:#999;}
</style>
